body {
  background-color: #b6c5ee;
  min-height: 500px;
  height: 100vh;
  display: flex;
  font-family: "OpenSans", "Arial", sans-serif; }

.btn {
  width: 100%;
  text-align: center;
  font-size: 14px;
  display: block;
  box-sizing: border-box;
  padding: 11px;
  border-radius: 5px;
  color: white;
  background-color: #4361e6;
  margin-bottom: 5px;
  text-decoration: none;
  transition: background-color 0.1s ease-in; }
  .btn:last-of-type {
    margin-bottom: 0; }
  .btn:hover {
    background-color: black;
    color: white; }
  .btn:active {
    background-color: #575757; }

.save:hover {
  background-color: #ffa3e4; }

.save:active {
  background-color: #ca68ad; }

.copy-html:hover {
  background-color: #ffcf86; }

.copy-html:active {
  background-color: #d18f51; }

canvas {
  margin: auto;
  max-width: 100%;
  max-height: 100%; }

.editor {
  padding: 30px;
  flex-basis: 800px;
  min-width: 550px;
  min-height: 300px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-content: flex-start;
  box-sizing: border-box;
  background-color: #ffffff;
  border-radius: 30px;
  margin: auto; }
  .editor__btn {
    padding: 10px;
    border: 1px solid #4361e6;
    background-color: transparent;
    color: #4361e6;
    border-radius: 5px;
    font-weight: 300;
    cursor: pointer;
    transition: background-color 0.1s ease-in;
    outline: none;
    font-size: 14px;
    margin-bottom: 20px; }
    .editor__btn:hover {
      background-color: #4361e6;
      color: #ffffff; }
    .editor__btn:active {
      background-color: #2c47be; }

.editor__form {
  width: 240px;
  min-height: 100px;
  display: flex;
  flex-direction: column;
  margin-right: 30px; }
  .editor__form > a {
    font-size: 14px;
    color: #000000;
    text-decoration: none;
    font-weight: bold;
    margin-bottom: 30px;
    letter-spacing: 1px;
    align-self: flex-start; }
  .editor__form h1 {
    font-family: "OpenSans", "Arial", sans-serif;
    margin: 0;
    padding: 0;
    font-weight: 300;
    font-size: 26px;
    color: #5a5a5a;
    margin-bottom: 20px; }
  .editor__form input {
    margin-bottom: 20px;
    box-sizing: border-box;
    font-size: 14px; }
  .editor__form label {
    font-size: 14px;
    color: #616161;
    font-weight: 300;
    margin-bottom: 10px; }
  .editor__form input[type="text"] {
    padding-bottom: 10px;
    padding-top: 10px;
    border: none;
    border-bottom: 1px solid #d3d3d3; }
  .editor__form textarea {
    margin-bottom: 10px;
    resize: none;
    height: 50px;
    border-radius: 5px;
    box-sizing: border-box;
    padding-left: 10px;
    padding-top: 5px;
    border-color: #d3d3d3; }

.editor__preview {
  max-width: 500px;
  flex-grow: 1;
  box-sizing: border-box;
  background-image: url("../img/bg.png");
  background-size: 5px 5px;
  display: flex;
  border-radius: 30px; }
